import Vue from 'vue'
import Router from 'vue-router'
import App from '../App.vue'
import home from '@/components/home'
import login from '../page/login.vue'
import article from '../page/article.vue'
import project from '../page/project.vue'
import activity from '../page/activity.vue'
import mall from '../page/mall.vue'
import circle from '../page/circle.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      	path: '/',
      	name: 'home',
      	component: home
    },
    {
      	path: '/home',
      	name: 'home',
      	component: home
    },
    {
    	path: '/login',
    	name: 'login',
    	component: login
    },
    {
    	path: '/article',
    	name: 'article',
    	component: article
    },
    {
    	path: '/project',
    	name: 'project',
    	component: project
    },
    {
    	path: '/activity',
    	name: 'activity',
    	component: activity
    },
    {
    	path: '/mall',
    	name: 'mall',
    	component: mall
    },
    {
    	path: '/circle',
    	name: 'circle',
    	component: circle
    }
  ]
})



